/* Imports */

/* This was installed via NPM first https://github.com/necolas/normalize.css/ */
@import "normalize.css";

/*  mobile:  425px / 26.5625em;
    tablet:  740px, / 46.250em;
    tablet (well when the design breaks):  850px, / 53.125em;
    desktop: 980px / 61.250em;
    wide:    1300px / 81.250em;
*/

@custom-media --phone-viewport (width <= 26.5625em);
@custom-media --tablet-viewport (width >= 26.5625em) and (width <= 53.125em);
@custom-media --desktop-viewport (width >= 53.125em) and (width <= 61.250em;);
@custom-media --wide-viewport (width >= 61.250em) and (width <= 81.250em;);

@lost gutter 16px;

/* Base grey colour */
$grey-skeleton: #343133;

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
/* Awesome image plugin by Drew Wilson */
/* https://github.com/drewwilson/atvImg */

.atvImg {
	border-radius: 5px;
	transform-style: preserve-3d;
	-webkit-tap-highlight-color: rgba(#000,0);
}

.atvImg img {
	border-radius: 5px;
	box-shadow: 0 2px 8px rgba(14,21,47,0.25);
}

.atvImg-container {
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 5px;
	transition: all 0.2s ease-out;
}

.atvImg-container.over .atvImg-shadow {
	box-shadow: 0 45px 100px rgba(14,21,47,0.4), 0 16px 40px rgba(14,21,47,0.4);
}

.atvImg-layers {
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 5px;
	overflow: hidden;
	transform-style: preserve-3d;
}

.atvImg-rendered-layer {
	position: absolute;
	width: 104%;
	height: 104%;
	top: -2%;
	left: -2%;
	background-repeat: no-repeat;
	background-position: center;
	background-color: transparent;
	background-size: cover;
	transition: all 0.1s ease-out;
}

.atvImg-shadow {
	position: absolute;
	top: 5%;
	left: 5%;
	width: 90%;
	height: 90%;
	transition: all 0.2s ease-out;
	box-shadow: 0 8px 30px rgba(14,21,47,0.6);
}

.atvImg-shine {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	border-radius: 5px;
	background: linear-gradient(135deg, rgba(255,255,255,.25) 0%,rgba(255,255,255,0) 60%);
}


